.PayWrap{
    display: none;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background:rgba(0,0,0,.91);

    .PayMain{
        position: relative;
        width: 100%;
        height: 100%;

        .Pay{
            position: absolute;
            top: 23%;
            left: 9%;
            width: 83%;
            height: 45vh;
            background:url(./images/payBck.png) no-repeat center center ;
            background-size:100% 100%;
            box-sizing:border-box;
            padding:29px 0;

            h1{
                color: #fff;
                text-align: center;
                font-size:0.48rem;
            }
            .zhifubao{
                margin: auto;
                width: 80%;
                height: 10vh;
                background:url(./images/zhifubao.png) no-repeat center center;
                background-size:100% ;
            }
            .weChat{
                margin: auto;
                width: 80%;
                height: 10vh;
                background:url(./images/weChat.png) no-repeat center center;
                background-size:100% ;
            }
        }

        .Close{
           position: absolute;
           top: 77%;
           left: 3%;

           .CloBtn{
            display: inline-block;
            position: absolute;
            top: 1%;
            left: 44%;
            margin: auto;
            width: 12%;
            height: 91%;
            // background:rgba(138, 42, 42, 0.3);
           }
        //    margin-top: 0;
            // margin-bottom: 0%;
        }
    }
}