.loading{
  display: none;
  margin: 0 auto;
  margin-top:100px;
  position: fixed;
  left: 32%;
  top: 3%;
  z-index: 999999;
  width: 34%;
  height: 3.5rem;
  background:rgb(0, 0, 0);
  border-radius: 10px;

  .spinner{
    position: relative;
    width: 0px;
    top: 47%;
    left: 50%;

    div{
      position: absolute;
      top: -2px;
      opacity: 0.25;
    }
    span{
      position: absolute;
      width: 11px;
      height: 4px;
      background: rgb(255, 255, 255);
      box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 1px;
      transform-origin: left center 0px;
      border-radius: 2px;
    }
    div:nth-child(1){
      animation: opacity-60-25-0-9 1s linear infinite;

      span{
        transform: rotate(0deg) translate(10px, 0px);
      }
    }
    div:nth-child(2){
      animation: opacity-60-25-1-9 1s linear infinite;

      span{
        transform: rotate(40deg) translate(10px, 0px);
      }
    }
    div:nth-child(3){
      animation: opacity-60-25-2-9 1s linear infinite;
      span{
        transform: rotate(80deg) translate(10px, 0px);
      }
    }
    div:nth-child(4){
      animation: opacity-60-25-3-9 1s linear infinite;
      span{
        transform: rotate(120deg) translate(10px, 0px);
      }
    }
    div:nth-child(5){
      animation: opacity-60-25-4-9 1s linear infinite;
      span{
        transform: rotate(160deg) translate(10px, 0px);
      }
    }
    div:nth-child(6){
      animation: opacity-60-25-5-9 1s linear infinite;
      span{
        transform: rotate(200deg) translate(10px, 0px);
      }
    }
    div:nth-child(7){
      animation: opacity-60-25-6-9 1s linear infinite;
      span{
        transform: rotate(240deg) translate(10px, 0px);
      }
    }
    div:nth-child(8){
      animation: opacity-60-25-7-9 1s linear infinite;
      span{
        transform: rotate(280deg) translate(10px, 0px);
      }
    }
    div:nth-child(9){
      animation: opacity-60-25-8-9 1s linear infinite;
      span{
        transform: rotate(320deg) translate(10px, 0px);
      }
    }

    @keyframes opacity-60-25-0-9 {
      0% {
        opacity: 0.25;
      }
      0.01% {
        opacity: 0.25;
      }
      0.02% {
        opacity: 1;
      }
      60.01% {
        opacity: 0.25;
      }
      100% {
        opacity: 0.25;
      }
    }

    
    @keyframes opacity-60-25-1-9 {
      0% {
        opacity: 0.25;
      }
      11.1211% {
          opacity: 0.25;
      }
      11.1311% {
          opacity: 1;
      }
      71.1211% {
          opacity: 0.25;
      }
      100% {
          opacity: 0.25;
      }
    }
    @keyframes opacity-60-25-2-9 {
      0% {
        opacity: 0.25;
      }
      22.2322% {
          opacity: 0.25;
      }
      22.2422% {
          opacity: 1;
      }
      82.2322% {
