      }
    }
    @keyframes opacity-60-25-3-9 {
      0% {
        opacity: 0.25;
      }
      33.3433% {
          opacity: 0.25;
      }
      33.3533% {
          opacity: 1;
      }
      93.3433% {
          opacity: 0.25;
      }
      100% {
          opacity: 0.25;
      }
    }
    @keyframes opacity-60-25-4-9 {
      0% {
        opacity: 0.305681;
      }
