.posWrap{
    width: 100%;
    height: 100%;
    position: fixed;
    background:url(./images/postageBck.png) no-repeat center center;
    background-size: 100% 100%;


    // 头部标题
    .posHeaderWrap{
        position: relative;
        width:100%;
        box-sizing: border-box;

        .posHeader{
            width:100%;
            height: 22vh;
            text-align: center;

            .posTil{
                text-align: center;
                line-height: 22vh;
                color: #fff;
                font-size: 1.4rem;
            }
        }


    }

    // 用户头像
    .posUserWrap{
        width:100%;
        position: relative;
        box-sizing: border-box;
        padding: 0px 0px;

        .posUser{
            position: absolute;
            display: flex;
            flex-direction: inherit;
            top: 14%;
            left: 19.82%;
            width:62%;
            height: 78%;
            // background:rgba(15,151,31,.3);

            .posUserImg , .posUserId{
                display: inline-block;
               
                height: 79%;
            }
            .posUserImg{
                width: 20.2%;
                // background:rgba(15,53,51,.3);
                border-radius: 100%;
            }
            .posUserId{
                width: 72%;
                line-height: 1.4rem;
                // margin-left: 10px;
                color: #fff;
                text-align: center;
                // background:blue;
                font-size: 0.48rem;
            }
        }
    }

    // 支付方式
    .posPayWrap{
        width: 100%;
        position: relative;
        box-sizing: border-box;
        padding:35px 0px;

        .posPay{
            width: 100%;
            position: relative;

            h1{
                color: #fff;
                text-align: center;
            }

            .posPayItem1{
                width: 100%;
                position: relative;
                margin-top:30px;
            }
            .posPayItem2{
                width: 100%;
                position: relative;
                margin-top:30px;
            }
            .posPayItemBtn{
                position: absolute;
                top: 0%;
                left: 17%;
                width: 66%;
                height:85%;
                // background:rgba(15,155,35,.3);
            }
        }
    }
}