.TipsWrap{
    width: 100%;
    position: fixed;
    top: -40%;
    left: 0%;
    transition: all  .7s linear;
    z-index: 99999;

    .TipsMain{
        position: relative;
        width: 100%;
        height: 1.5rem;
        background:rgb(16, 156, 11);
        margin: auto;
        text-align: center;

        span{
            color: #fff;
            font-size: 0.38rem;
        }
    }
}