.ActSucWrap{
    width: 100%;
    height: 100%;
    position: fixed;
    background:url('./images/SuccessBck.png') no-repeat center center;
    background-size:100% 100%;

    // 头部部分
    .ActSucHeaderWrap{
        width: 100%;
        position: relative;

        .SucInfo{
            position: absolute;
            top: 88%;
            left: 0%;
            box-sizing: border-box;
            padding: 0 20px;
            width: 100%;
            height: 3.9vh;
            // background: red;
            text-align: center;

            .SucInfoTxt{
                font-size: 2vh;
                color: #fff;
                
            }
        }
    }

    // 礼物展示部分
    .SucImgWrap{
        width: 100%;
        position: relative;
        box-sizing: border-box;
        padding: 22px 55px;
        
        .SucImgMain{
            position: relative;
            width: 100%;
            height: 56vh;
            background: url(./images/SucImgWrap.png) no-repeat center center;
            background-size:100% 100% ;

            .SucImgTil{
                position: absolute;
                top: 23%;
                left: 16%;
                width: 70%;
                height: 3.6vh;
                // background: red;
                background: url(./images/SucImgTil.png) no-repeat center center;
                background-size:100% ;
                color: #fff;
                font-size: 0.15rem;
                text-align: center;
                line-height: 3.6vh;
            }

            .SucImg{
                position: absolute;
                top: 33%;
                left: 10%;
                width: 80%;
                height: 23vh;
                // background:red;
                background: url(./images/SuccImg.png) no-repeat center center;
                background-size:100% 100%;
            }
            .SucBtn{
                position: absolute;
                top: 80%;
                left: 16%;
                width: 70%;
                height: 6.2vh;
                // background:red;
                background: url(./images/SucBtn.png) no-repeat center center;
                background-size:100% 100%;

                .SucB{
                    display: block;
                    width: 100%;
                    height: 100%;
                    background:rgba(138, 42, 42, 0.3);
                }
            }
        }
    }
}