.ActiveWrap{
    width: 100%;
    height: 100%;
    
    .Active{
        width: 100%;
        height: 100%;
        position: relative;
        background:url(./images/ActiveBck.png) no-repeat center center;
        background-size: 100% 100%;
    }

    // 头部标题
    .ActHeaderWrap{
        width: 100%;
        position: relative;

        .ActHeader{
            width: 100%;
            position: relative;
        }
    }

    // 用户头像
    .ActUserWrap{
        width:100%;
        position: relative;
        box-sizing: border-box;
        padding: 0px 0px;
        margin-top: 10px;

        .ActUser{
            position: absolute;
            display: flex;
            flex-direction: inherit;
            top: 15%;
            left: 19.6%;;
            width:62%;
            height: 69%;
            // background:rgba(15,151,31,.3);

            .ActUserImg , .ActUserId{
                display: inline-block;
               
                height: 87%;
            }
            .ActUserImg{
                width: 20.6%;
                // background:rgba(15,53,51,.3);
                border-radius: 50px;
                background:url(./images/img.png) no-repeat center center;
                background-size:100% 100%;
            }
            .ActUserId{
                width: 72%;
                line-height:1.2rem;
                // margin-left: 10px;
                color: #fff;
                text-align: center;
                // background:blue;
                font-size: 0.48rem;
            }
        }
    }
    // 礼品图片
    .ActiveImg{
        width: 100%;
        position: relative;

        .ActiveImgWrap{
            width: 100%;
            height: 5vh;
            background: url(./images/ActiveImgTil.png) no-repeat center center;
            background-size: 100% 100%;
            margin: auto;
        }
    }
    // 助力进度
    .ActivePro{
        width: 100%;
        position: relative;
        margin-top: -1%;

        .ActivePro-title{
            width: 100%;;
            color: #fff;
            text-align: center;
            font-size: .40rem;
        }

        .ActivePro-main{
            position: relative;
            width: 63%;
            line-height: 1rem;
            margin: auto;

            span{
                display: inline-block;
                position: relative;
                width: 100%;
                line-height: 0.1rem;
                height: 0.18rem;
                border-radius: 10px;
                background:rgb(57,57,67);
            }
            .finish{
                position: absolute;
                top: 0px;
                left: 0px;
                width: 50%;
                height: 100%;
                background:rgb(255,199,0);
            }

            .ActiveProOne{
                position: absolute;
                // display: flex;
                top: 56%;
                left: 48%;
                width: 12%;
                background: transparent;      
            }

            .ActiveProTwo{
                position: absolute;
                // display: flex;
                top: 56%;
                right: -13px;
                width: 12%;
                background: transparent;

                .ActiveProCri{
                    background: transparent;
                }
            }
            .ActiveProCri{
                width: 0.35rem;
                height: 0.35rem;
                border-radius: 100%;
                // border:1px solid black;
                // box-shadow: 0px 0px  20px 7px rgb(255,255,0);
                // background: rgba(255, 255, 0,.5);
                // background:rgb(255,255,255)
                background: rgb(255,255,0);
            
            }
            .ActiveProCri1{
                width: 0.25rem;
                height: 0.25rem;
                border-radius: 100%;
                background:transparent;
            }
            .ActiveProOP{
                display: block;
                font-size: 0.4rem;
                color: #fff;
                background: transparent;
                margin-top: 73%;
            }
        }
    }
    // 无需助力
    .ActPayBck{
        position: relative;
        width: 100%;
        height: 6rem;

    }
    .ActPayWrap{
        width: 45%;
        line-height: 0.08rem;
        // margin: 0.48rem auto;
        box-sizing: border-box;
        padding: 50px 0px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 11px;
        

        .ActPay{
            width: 100%;
            height: 100%;
            // background:Red;
            text-align: center;
            border-bottom: 1px solid rgb(255,199,0);

            span{
                color:rgb(255,199,0);
                line-height: 0.55rem;
                font-size: .48rem;
            }
        }
    }
    // 领取按钮
    .ActBtnWrap{
        position: fixed;
        width: 100%;
        bottom: 0;
        margin-bottom: 17%;
        .ActBtn{
            width: 100%;
        }
        .ActB{
            display: inline-block;
            position: absolute;
            top: 0%;
            margin: auto;
            width: 100%;
            height: 3rem;
            // background:rgba(138, 42, 42, 0.3);
            background: #ffc400;

            p{

                height: 100%;
                line-height: 1.7rem;
                text-align:center;
                font-size:.48rem;
                font-weight: 600;
                color: #000;
            }
        }
    }
    // 保存图片遮罩层
    .imgWrap{
        position: fixed;;
        bottom: 0;
        left:0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,.91);
    }
    // 保存图片
    .saveImgWrap{
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        background:white;
        z-index: 99;
        box-sizing:border-box;
        padding: 0.1rem 0;
        margin:0 auto;

        .saveImg{
            width: 100%;
            height: 100%;
            // background:red;
            margin:0 auto;
            // display: flex;
            img{
                display: block;
                margin:0 auto;
            }

            span{
                font-size: 0.5rem;
                display: block;
                // width: 20%;
                margin-top: 10px;
                text-align: center;
            }
        }
    }
    // 二维码遮罩层
    .codeWrap{
        display: none;
        position: fixed;;
        bottom: 0;
        left:0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,.91);
        z-index: 999;    
    }
    .saveCode_Wrap{
        position: relative;
        width: 100%;
        height: 100%;
    }
    .saveCodeWrap{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9999;

        .closeCode{
            display: none;
            position: fixed;
            top: 2.6%;
            right: 9%;

            span{
                display: inline-block;
                width: 30px;
                height: 30px;
                // background: url(./images/)
                background: url(./images/Act_Suc_Close_item.png) no-repeat center center;
                background-size: 100%;
            }
        }

        canvas{
            width: 70%;
            margin-left: 55px;
            border:1px solid #000000;
            background-color: #000
        }
        #qrcode{
            width:100px; 
            height:100px;
            display: none;
        }
        .codeImg{
            margin-top: 1%;
            width: 77%;
            margin-left: 11.5%;
            z-index: 9999;
        }
    }

    // 保存图片按钮
    .SaveImgBtnWrap{
        display: none;
        position: fixed;
        bottom: 5.5%;
        left: 0;
        width: 100%;
        line-height: 1rem;
        // background:red;
        box-sizing: border-box;
        padding: 12px 63px;
        z-index: 999999;

        .SaveImgBtn{
            
            width: 100%;
            background:#ff6700;
            border-radius: 30px;
            text-align: center;

            span{
                color: #fff;
                font-size: 0.58rem;
                line-height: 1.2rem;
            }
        }
    }

    // 助力成功
        //助力成功遮罩层
    .ActSucWrap{
        display: none;
        position: fixed;;
        bottom: 0;
        left:0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,.91);   
    } 
        // 助力成功弹窗
    .ActSuccessWrap{
        display: none;
        position: fixed;
        top: 33%;
        left: 0;
        width: 100%;

        .ActSuccess_Wrap{
            position: relative;
            width: 100%;
            height: 100%;

            .ActSuccess_btn{
                display: inline-block;
                position: absolute;
                top: 64%;
                left: 23%;
                margin: auto;
                width: 54%;
                height: 21%;
                text-align: center;
                font-size: 0.48rem;
                line-height: 7vh;
            }
        }
        .CloseActSuccess{
            position: absolute;
            top: 1%;
            right: 12%;

            span{
                display: inline-block;
                width: 30px;
                height: 30px;
                // background: url(./images/)
                background: url(./images/Act_Suc_Close_item.png) no-repeat center center;
                background-size: 100%;
            }
        }
        // .CloseActSuccess{
        //     position: relative;
        //     width: 100%;
        //     text-align: center;

        //     .CloseActSuccess_btn{
        //         display: inline-block;
        //         position: absolute;
        //         top: 0%;
        //         left: 44%;
        //         margin: auto;
        //         width: 12%;
        //         height: 91%;
        //     }
        // }
    }
}
