.shareWrap{
    display: none;
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background:rgba(0,0,0,.5);

    .shareMain{
        width: 100%;
        height: 100%;
        position: relative;

        // 二维码
        .SaveCodeWrap{
            width: 100%;
            height: 39vh;
            // background:red;
            margin-top:3px;
            margin-bottom: 10px;
            box-sizing: border-box;
            padding: 0 20px;
            overflow: hidden;

            .SaveCode{
                width: 100%;
                height: 100%;
                background:rgb(18,18,58);
                overflow: hidden;

                .SaveCodeImg{
                    width: 60%;
                    height: 20vh;
                    // background:red;
                    margin: 27px auto;
                }
            }
        }

        // 关闭按钮
        .SaveCloBtnWrap{
            width: 100%;
            height: 8.7vh;
            // background:red;

            .SaveCloBtn{
                display:block;
                width: 17%;
                height: 100%;
                // background:blue;
                margin: auto;
            }
        }
    }
}