.addlistArea_fixbox{
    position: fixed;
    bottom: 0;
    left:  0 ;
    right: 0;
    top: 0;
    z-index: 8;

    .addlistArea_boxbg{
        position: absolute;
        bottom: 0;
        left : 0;
        right: 0;
        height: 8.08rem;
        background: white;
        z-index: 5;

        .addlistArea_box{
            overflow: hidden;
            width: 95%;
            height: 8.08rem;
            margin: 0 auto;

            .addlistArea_navbar{
                float: left;
                overflow: hidden;

                li{
                    float: left;
                    font-size: 0.4rem;
                    color: black;
                    padding: 0.3rem 0.15rem;
                    position: relative;
                }
            }

            .addlistArea_navbar_conbtn{
                padding-top: 0.1rem;
                float: right;
                color: #000;
                line-height: 1rem;
                font-size: 0.4rem;
                margin-right: 0.15rem;
            }
        }
    }

    .line_insert{
        height: 1px;
        background: #e4e4e4;
        opacity: 0.15;
    }
    .addlistArea{
        width: 100%;
        margin: 0 auto;
        overflow-y: auto;
        height: calc(100% - 1.5rem);

        li{
            padding: 0 0.05rem;
            font-size: 0.4rem;
            line-height: 0.8rem;
        }
    }
}