.UserInfoWrap{
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    background:url(./images/UserInfoBck.png) no-repeat center center;
    background-size: 100% 100%;

    // 头部部分
    .UIHeaderWrap{

        .UIHeader{
            width: 100%;
            position: relative;

            .UIReturn{
                display: inline-block;
                position: absolute;
                top: 20%;
                left: 4%;
                width: 4%;
                height: 45%;
                // background: rgba(0,0,0,.2);
                
            }
        }
    }


    // 信息填写部分
    .UIMainWrap{
        width: 100%;
        position: relative;

        .UIMain{
            width: 100%;
            position: relative;

            .InfoForm{
                position: relative;
                width: 100%;
    
                form{
                    input{
                        height: 100%;
                        background:transparent;
                        outline: none;
                        border-color: transparent;
                        color: white;
                        font-size: 0.38rem;
                        margin-left: 0.2rem
                    }
                    label{
                        display: inline-block;
                        text-align: left;
                        width: 15%;
                        color: rgb(128,128,128);
                        font-size: 0.28rem;
                        margin-left:0.5rem;
                    }
    
                    .user , .InfoPhone{
                        width: 100%;
                        // height: 1rem;
                        line-height: 1rem;
                        border-bottom: 1px solid rgb(77,77,77);
                    }
                    .AddressInfo{
                        border-bottom: 1px solid rgb(77,77,77);
                    }
                    .InfoPay{
                        width: 100%;
                        height: 8vh;
                        line-height: 6.9vh;
    
                        span{
                            font-size: 2vh;
                            color: #fff;
                        }
                        .OldP{
                            text-decoration: line-through;
                            color: #333;
                        }
                    }
    
                    .InfoAddress , .AddressInfo {
                        width: 100%;
                        // height: 6rem;
                        line-height: 1.3rem;

                        p{
                            display: inline-block;
                            // background: red;
                            font-size: 0.38rem;
                            width: 70%;
                            height: 100%;
                            margin-left: 0.2rem;
                        }
                    }
                }
            }
        }
    }

    // 确定按钮
    .InfoFooter{
        position: relative;
        width: 100%;
        height: 100%;
        background: rgba(255,255,255,.1);

        .InfoB{
            display: inline-block;
            position: absolute;
            top: 6%;
            left: 16%;
            margin: auto;
            width: 69%;
            height: 8%;
            // background:rgba(138, 42, 42, 0.3);
        }
    }

}