.LoginWrap{
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    background:url(./images/LoginBck.png) no-repeat center center;
    background-size:100% 100%;

    // 头部部分
    .LoginHeaderWrap{
        display: block;
        width: 100%;
        position: relative;

        .LoginHeader{
            width: 100%;
            position: relative;

            .LoginReturn{
                display: inline-block;
                position: absolute;
                top: 20%;
                left: 4%;
                width: 4%;
                height: 45%;
                // background: rgba(0,0,0,.2);
                
            }
        }
    }

    // 登录界面
    .LoginMainWrap{
        position: relative;
        width: 100%;
        box-sizing:border-box;
        padding:40px 0px;
        
        .LoginMain{
            width: 100%;
            height: 30vh;
            // background:red;

            .LoginForm{
                width: 100%;
                position: relative;
                margin-top: 30px;

                .phone , .code{
                    margin: auto;
                    width: 67%;
                }

                .phone{
                    
                    width: 67%;
                    height: 100%;
                    position: relative;
                    display: table;
                        .code{
                            display: table;
                            position: absolute;
                            top: 0;
                            right: 0;
                            width: 42%;
                            height: 100%;
                            background:rgb(109, 175, 129);
                            border-top-right-radius: 5px;
                            border-bottom-right-radius: 5px;
                            display:table-cell;
                            vertical-align:middle;
                            text-align: center;
                            line-height: 1rem;
                            // line-height: 250%;
                            color: #fff;
                            font-size: 0.38rem;
                        }
                        .Ucode{
                            display: table;
                            position: absolute;
                            top: 0;
                            right: 0;
                            width: 42%;
                            height: 100%;
                            background:#333;
                            border-top-right-radius: 5px;
                            border-bottom-right-radius: 5px;
                            display:table-cell;
                            vertical-align:middle;
                            text-align: center;
                            line-height: 1rem;
                            // line-height: 250%;
                            color: gray;
                            font-size: 0.38rem;
                        }
                    
                    }

                    
                


                input{
                    display: block;
                    width: 100%;
                    height: 1rem;
                    text-indent: 0.3rem;
                    border-radius: 3px;
                    color: #fff;
                    border: none;
                    outline: none;
                    background: url(./images/PhoneWrap.png) no-repeat center center;
                    background-size:100% 100%;
                    font-size: 0.38rem;
                    margin: auto;
                }

            }

            .LoginBtn{
                width: 100%;
                position: relative;
                margin-top: 30px;

                .LoginB{
                    display: inline-block;
                    position: absolute;
                    top: 5%;
                    left: 16%;
                    margin: auto;
                    width: 68%;
                    height: 82%;
                    // background:rgba(138, 42, 42, 0.3);
                }
            }


        }
    }
}