.HPWrap{
    width: 100%;
    height: 100%;
    // position: flex;
    position: fixed;
    background:url(./images/bck.png) no-repeat center center;
    background-size:100%;

    h1{
        text-align: center;
        color: #c7906c;
    }

    button{
        width: 30%;
        height: 1rem;
        margin-top: 10%;
        margin-left:  35%;
    }

    // 已领取过
    .gottenWrap{
        display: none;
        width: 100%;
        height:100%;
        position: fixed;
        top: 0;
        left: 0;
        background:rgba(0,0,0,.91);
    }

    .gotten{
        display: none;
        position: absolute;
        top: 12%;
        left: 0;
        width: 100%;

        .gotten_wrap{
            position: relative;
            width: 100%;
            height: 100%;
                .gotten_btn{
                    display: inline-block;
                    position: absolute;
                    top: 62%;
                    left: 23%;
                    margin: auto;
                    width: 54%;
                    height: 21%;
                    // background: rgba(128, 26, 26, 0.5);
                }
            
        }

        .closeGotten{
            position: relative;
            width: 100%;
            text-align: center;
            .closeBtn{
                display: inline-block;
                position: absolute;
                top: 14%;
                left: 44%;
                margin: auto;
                width: 12%;
                height: 82%;

            }
        }
    }
}